/* Полупрозрачный оверлей для блокировки взаимодействия с фоном */
.bpm-popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1000;
    overscroll-behavior: none;
    touch-action: none;
    -webkit-overflow-scrolling: none;
  }
  
  /* Обертка для всплывающего меню в центре экрана */
  .bpm-popup-wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -65%); /* Смещение вверх */
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: auto;
    overscroll-behavior: none;
    touch-action: none;
    -webkit-overflow-scrolling: none;
  }
  
  /* Стили для BPM контролов */
  .bpm-controls {
    display: flex;
    align-items: center;
    background: linear-gradient(135deg, #00b478, #00c488);
    padding: 14px 16px;
    border-radius: 12px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
    position: relative;
    /* Важные свойства для блокировки скролла */
    touch-action: none;
    -webkit-overflow-scrolling: none;
    overscroll-behavior: none;
    overflow: hidden;
    cursor: ns-resize;
  }
  
  .bpm-buttons {
    display: flex;
    flex-direction: column;
    gap: 4px;
  }
  
  .bpm-value {
    font-family: 'DrummerFont', sans-serif;
    font-size: 42px;
    font-weight: bold;
    color: white;
    min-width: 100px;
    text-align: center;
    text-shadow: 0 2px 3px rgba(0, 0, 0, 0.3);
    line-height: 1;
    padding: 4px 0;
    user-select: none;
    -webkit-user-select: none;
  }
  
  .bpm-button {
    font-family: 'DrummerFont', sans-serif;
    font-size: 24px;
    color: rgba(255, 255, 255, 0.9);
    background: rgba(0, 0, 0, 0.15);
    border: none;
    border-radius: 6px;
    padding: 2px 10px;
    cursor: pointer;
    text-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
    display: flex;
    align-items: center;
    justify-content: center;
    line-height: 1.2;
    -webkit-tap-highlight-color: transparent;
    user-select: none;
    -webkit-user-select: none;
  }
  
  .bpm-button:hover {
    background: rgba(0, 0, 0, 0.25);
    color: white;
  }
  
  .bpm-button:active {
    background: rgba(0, 0, 0, 0.3);
  }
  
  /* Бликовый эффект на контролах */
  .bpm-controls:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 40%;
    background: linear-gradient(to bottom, 
                             rgba(255, 255, 255, 0.2) 0%, 
                             rgba(255, 255, 255, 0) 100%);
    border-radius: 12px 12px 0 0;
    pointer-events: none;
  }
  
  /* Медиа-запрос для мобильных устройств */
  @media (max-width: 480px) {
    .bpm-controls {
      padding: 12px 14px;
    }
  
    .bpm-value {
      font-size: 36px;
      min-width: 90px;
    }
  
    .bpm-button {
      font-size: 20px;
      padding: 2px 8px;
    }
  
    .bpm-popup-wrapper {
      transform: translate(-50%, -70%); /* Еще выше на мобильных */
    }
  }